import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { InquireAndDisputeFile, InquireAndDisputeTopics, MessageForm } from '../../model/messages.model';
import { TransactionsDetailsLoadingState } from '../../model/transactions-details.model';

@Component({
  selector: 'bb-transactions-inquiry-dispute',
  templateUrl: 'transactions-inquiry-dispute.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TransactionsInquiryDisputeComponent implements OnInit, OnDestroy {
  @Input() message: MessageForm | undefined;
  @Input() enableDispute = false;
  /**
   * Whether the dispute & inquiry form is sending or not.
   */
  @Input() state = TransactionsDetailsLoadingState.notStarted;
  @Input() inquireAndDisputeTopics: InquireAndDisputeTopics | undefined;

  @Output() submitReport = new EventEmitter<MessageForm>();

  @Output() cancelReport = new EventEmitter<void>();

  @Output() messageChange = new EventEmitter<MessageForm>();

  @Output() fileChange = new EventEmitter<File>();
  @Output() fileRemoved = new EventEmitter<InquireAndDisputeFile>();

  confirmationDialogActive = false;
  labels = {
    inquire: $localize`:Inquire tooltip@@transactions.dispute.inquire.inquireLabel:Inquire for more information`,
    dispute: $localize`:Dispute tooltip@@transactions.dispute.inquire.disputeLabel:Dispute this transaction`,
  };

  readonly loadingStateEnum = TransactionsDetailsLoadingState;

  private unsubscribeSubject = new Subject<void>();

  transactionReportForm = this.fb.group({
    topic: [{ value: '' }, this.topicRequired],
    attachment: [undefined],
    subject: [{ name: '' }],
    body: [''],
  });

  private topicRequired(control: AbstractControl): Record<'required', string> | null {
    return control.value?.value === '' ? { required: 'value is required' } : null;
  }

  private getControl(field: string): AbstractControl | undefined {
    return this.transactionReportForm.controls[field];
  }

  private setFormValue(values: MessageForm) {
    this.transactionReportForm.patchValue(
      {
        body: values.body,
        attachments: values.attachments,
      },
      { emitEvent: false },
    );
  }

  hasError(field: string, type: string) {
    const control = this.getControl(field);

    return control && control.errors && control.errors[type];
  }

  isInvalidControl(field: string): boolean {
    const control = this.getControl(field);

    return !!control && control.touched && control.invalid;
  }

  saveReport() {
    if (this.transactionReportForm.valid) {
      this.submitReport.emit(this.message);
    } else {
      this.transactionReportForm.markAllAsTouched();
    }
  }

  closeReportForm() {
    this.cancelReport.emit();
    this.closeConfirmationDialog();
  }

  closeConfirmationDialog() {
    this.confirmationDialogActive = false;
  }

  processLabel(label: string) {
    const arr = label.split(' ');
    const last = arr.splice(arr.length - 1, 1);

    return [arr.join(' ') + ' ', ...last];
  }

  ngOnInit() {
    if (this.message) {
      this.setFormValue(this.message);
    }

    this.transactionReportForm.valueChanges.pipe(takeUntil(this.unsubscribeSubject)).subscribe({
      next: (formValue) => {
        this.messageChange.emit({ ...this.message, ...formValue });
      },
    });
  }

  uploadFile(input: HTMLInputElement) {
    if (input.files && input.files[0]) {
      const file: File = input.files[0];
      this.fileChange.emit(file);
      input.value = '';
    }
  }

  removeAttachment(attachment: InquireAndDisputeFile) {
    this.fileRemoved.emit(attachment);
  }

  ngOnDestroy() {
    this.unsubscribeSubject.next();
    this.unsubscribeSubject.complete();
  }

  constructor(private readonly fb: FormBuilder) {}
}
